import uniIcons from "@/components/uni-icons/uni-icons.vue";
export default {
	name: "UniSearchBar",
	components: {
		uniIcons
	},
	props: {
		placeholder: {
			type: String,
			default: "搜索"
		},
		radius: {
			type: [Number, String],
			default: 5
		},
		clearButton: {
			type: String,
			default: "auto"
		}
	},
	data() {
		return {
			show: false,
			showSync: false,
			showCancel: true,
			searchVal: ""
		}
	},
	watch: {
		searchVal(nv, ov) {
			this.$emit("input", {
				value: this.searchVal
			})
		}
	},
	methods: {
		searchClick() {
			this.searchVal = ""
			this.show = true;
			this.$nextTick(()=>{
				this.showSync = true;
			})
		},
		clear() {
			this.searchVal = ""
		},
		cancel() {
			this.$emit("cancel", {
				value: this.searchVal
			});
			this.searchVal = ""
			this.show = false
			this.showSync = false

			uni.hideKeyboard()




		},
		confirm() {

			uni.hideKeyboard();




			this.$emit("confirm", {
				value: this.searchVal
			})
		}
	}
};
